#ScrollButton {
  position: absolute;
  left: 50%;
  -webkit-transform: translate(-50%);
      -ms-transform: translate(-50%);
          transform: translate(-50%);
  bottom: 50px; }
  #ScrollButton .mouse {
    width: 15px;
    height: 30px;
    border: 1px solid #ffffff;
    border-radius: 10px;
    cursor: pointer;
    -webkit-transition: opacity 0.15s ease-in-out;
    -o-transition: opacity 0.15s ease-in-out;
    transition: opacity 0.15s ease-in-out; }
    #ScrollButton .mouse:hover {
      opacity: .5; }
    #ScrollButton .mouse .wheel {
      width: 3px;
      height: 3px;
      background-color: #ffffff;
      border-radius: 100%;
      position: relative;
      top: -2px;
      -webkit-animation: Wheel 2s ease infinite;
              animation: Wheel 2s ease infinite; }

@-webkit-keyframes Wheel {
  50% {
    top: 2px; } }

@keyframes Wheel {
  50% {
    top: 2px; } }



